.pivot{
    position: fixed;
    width: 0;
    height: 0;
    top: 50vh;
    right: 50vw;
}

.homeFace{
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 4;

    backdrop-filter: blur(0px);

    /*animation-name: faceA;*/
    animation-duration: 2s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
    animation-direction: normal;
}

@keyframes faceA{
    100%{
        backdrop-filter: blur(20px);
    }
}

.homeBack{
    position: absolute;
    width: 500vh;
    height: 500vh;
    left: -250vh;
    top: -250vh;
    /*background: linear-gradient(0deg, rgb(223, 63, 63), rgb(63, 223, 63), rgb(63, 63, 223));*/
    background: linear-gradient(0deg, rgb(191, 191, 191), rgb(239, 79, 79), rgb(79, 239, 79), rgb(191, 191, 191));
    z-index: 0;

    animation-name: backA;
    animation-duration: 18s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    animation-direction: normal;
    animation-iteration-count: infinite;
}

@keyframes backA{
    0%{
        rotate: 0deg;
        filter: hue-rotate(0deg);
    }
    100%{
        rotate: 360deg;
        filter: hue-rotate(360deg);
    }
}

.homeOutArea{
    position: absolute;
    width: 300vh;
    height: 300vh;
    left: -150vh;
    top: -156vh;
    z-index: 2;
    rotate: 0deg;

    animation-name: outAreaA;
    animation-duration: 36s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    animation-direction: normal;
    animation-iteration-count: infinite;
}

@keyframes outAreaA{
    0%{
        rotate: 0deg;
    }
    100%{
        rotate: -360deg;
    }
}

.homeInArea{
    position: absolute;
    width: 300vh;
    height: 300vh;
    left: -150vh;
    top: -150vh;
    z-index: 1;
}

.homeImg{
    position: absolute;
    width: 100%;
    height: 100%;
}

.homeText{
    position: absolute;
    text-align: center;
    width: 100vw;
    left: -50vw;
    top: 30vh;
    font-size: 2rem;
    color: white;
    z-index: 3;
}