.musicList_area{
    position: relative;
    margin: 2rem auto;
    padding: 0 0;
    width: 90%;
    /*background-color: azure;*/
}

.musicList_heading{
    position: relative;
    margin: 0 auto;
    padding: 0.2rem 0.2rem;
    font-size: 1.2rem;
    width: 100%;
    display: flex;
    justify-content: center;
    /*background-color: rgb(0, 255, 174);*/
}

.musicList_group{
    position: relative;
    overflow-y: scroll;
    margin: 0 0;
    padding: 0 0.2rem;
    width: 100%;
    height: 60vh;
    border-bottom: solid 2px black;
    border-top: solid 2px black;
    background-color: rgb(243, 243, 243);
}

.musicList{
    position: relative;
    margin: 0 0;
    padding: 0.2rem 0;
    font-size: 1.2rem;
    border-bottom: solid 1px black;
    width: 100%;
    display: flex;
    justify-content: center;
    /*background-color: rgb(0, 255, 174);*/
}

.musicList_musicName{
    position: relative;
    margin: 0 0;
    padding: 0.2rem 0.2rem;
    /*background-color: rgb(0, 255, 255);*/
    flex-basis: 30%;
}

.musicList_musicianName{
    position: relative;
    margin: 0 0;
    padding: 0.2rem 0.2rem;
    /*background-color: rgb(255, 242, 0);*/
    flex-basis: 30%;
}

.musicList_regisrationDate{
    position: relative;
    margin: 0 0;
    padding: 0.2rem 0.2rem;
    /*background-color: rgb(255, 187, 0);*/
    flex-basis: 30%;
}

.musicList_buttons{
    position: relative;
    margin: 0 0;
    padding: 0 0;
    /*background-color: rgb(8, 0, 255);*/
    flex-basis: 10%;

    display: flex;
    justify-content: right;
}

.musicList_headingOthers{
    position: relative;
    margin: 0 0;
    padding: 0.2rem 0.2rem;
    /*background-color: rgb(255, 0, 187);*/
    flex-basis: 12%; /*ちょっと大きめ*/
}

.musicList_button{
    position: relative;
    margin: 0 0.2rem;
    aspect-ratio: 1/1;
    height: 2.2rem;
    /*background-color: aliceblue;*/
}

.musicList_buttonImage{
    position: relative;
    display: block;
    height: 100%;
    margin: 0 auto;
    /*background-color: rgba(128, 255, 0, 0.497);*/
}